.inputData {
    text-align: center;
    margin-top: 0.5rem;
    position: relative;
}

.inputField {
    width: 60%;
    outline: none;
    background: none;
    border-bottom: 2px solid rgb(61 52 81);
    border-top: none;
    border-right: none;
    border-left: none;
    font-size: 1.4rem;
    padding: 0.8rem;
    color: rgb(231, 197, 197);
}

::placeholder {
    color: rgb(231, 197, 197);
    font-size: 1.4rem;
}

.head {
    display: grid;
    place-items: center;
    margin-top: 5rem;
}

.wi {
    color: rgb(231, 197, 197);
    font-size: 5rem;
    animation: scale 2s ease-in-out infinite alternate-reverse;
}

.desc {
    color: rgb(231, 197, 197);
    text-align: center;
    text-transform: capitalize;
    padding-top: 2rem;
    font-size: 1.3rem;
    animation: space 2s ease-in-out infinite alternate-reverse;
}

.main {
    color: rgb(249 198 155);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.location {
    text-align: center;
    font-size: 2.4rem;
    text-transform: capitalize;
    padding: 1rem;
    z-index: 99999;
    animation: move 2s ease-in-out infinite alternate-reverse both;
}

.temp {
    padding-top: 1rem;
    z-index: 99999;
}

.min_max {
    font-size: 1.4rem;
    font-weight: 400;
    padding-top: 0.4rem;
    z-index: 9999;
}

.time {
    z-index: 9999;
    color: rgb(231, 197, 197);
    font-size: 1.2rem;
    padding-top: 0.4rem;
    font-weight: 400;
}

.fa-search {
    font-size: 1.8rem;
    color: rgb(61 52 81);
    cursor: pointer;
    position: absolute;
    top: 1rem;
    transition: all .3s ease;
}

.fa-search:hover {
    font-size: 2rem;
}


/* .fa-location-arrow {
    animation: animate 2s ease-in-out infinite alternate-reverse;
} */

.error {
    color: rgb(231, 197, 197);
    text-align: center;
    padding-top: 2rem;
    font-size: 1.6rem;
}


