    }
} */

@keyframes move {
    0% {
        transform: translateY(-10px)
    }
    100% {
        transform: translateY(10px);
    }
}

@keyframes scale {
    0% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes space {
    0% {
        letter-spacing: 0.4rem;
    }
    100% {
        letter-spacing: 1px;
    }
}


